import React from 'react'
import { Rate } from 'antd'

interface PinPropsType extends React.ComponentProps<typeof Rate> {
  checked: boolean;
  onCheckedChange?: (checked: boolean) => void
}

export const Pin: React.FC<PinPropsType> = (props) => {
  const { checked, onCheckedChange, ...restProps } = props

  return <Rate
    count={1}
    value={checked ? 1 : 0}
    onChange={num => onCheckedChange?.(!!num)}
    {...restProps}
  />
}
